
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import HlCell from '../../base/hl-cell/hl-cell'
import { isWeiXin } from '../../util/util'
import { mapMutations } from 'vuex'
import Loading from '../../base/loading/loading'
import wx from 'weixin-js-sdk'
import api from '../../fetch/api'











export default {
props: {
itemList: {
type: Array,
default: '全部'
},
isLoading: {
type: Boolean,
default: false
}
},
data () {
return {
shopName: '小丸子纹绣店',
pagState: '待付款',
type: '1',
list: [{
src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '广州韩束定妆广州韩束定妆广州韩束定妆',
desc: '由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。',
url: '/component/cell'
}],
orderType: 9,
titleNo: '没有更多的订单~'
}
},
created () {
this.signApi()
},
methods: {
/* @vOrderType 订单类型：1.普通订单 2.团购拼单 3. 砍价订单 */
toDetail (vGroupOrderGuid, vOrderGuid, vOrderType, vOrderState) {
if (vOrderType === 2 && vOrderState === 9) {
this.$router.push({path: '/groupdetail', query: {vGroupOrderGuid: vGroupOrderGuid}})
} else if (vOrderType === 2 && vOrderState === 2) {
this.$router.push({path: '/groupSuccess', query: {vGroupOrderGuid: vGroupOrderGuid, vOrderGuid: vOrderGuid}})
} else if (vOrderState === 1 || vOrderState === 2 || vOrderState === 3 || vOrderState === 5 || vOrderState === 6 || vOrderState === 7 || vOrderState === 8) {
this.$router.push({path: '/orderdetail', query: {vOrderGuid: vOrderGuid}})
}
},
cancelOder (vOrderGuidVal) {

let than = this
this.$confirm('提示', '是否确定取消该订单?', function () {
than.getCancelOder(vOrderGuidVal)
})
},
getCancelOder (vOrderGuidVal) {

let that = this
api.cancelOrder({

vOrderGuid: vOrderGuidVal
}).then(res => {
if (res.code === 0) {

this.$msgbox('提示', '取消成功', function () {
that.$emit('cancelOrder')
})
}
}, err => {
this.$msgbox('提示', err.data.message)
})
},
doPay (orderGuid) {
let that = this
isWeiXin(function () {
that.toPay(orderGuid)
}, function () {
let options2 = {
'orderType': that.orderType,
'payway': 2,
'orderGuid': orderGuid
}
let jsonStr = JSON.stringify(options2)
that.anzhuoPay(jsonStr)
})
},
toPay (orderGuid) {

api.doPay({

orderGuid: orderGuid,
orderType: this.orderType
}).then(res => {
console.log('调起支付成功')
console.log(res)
let options = res.data
this.wxPay(options)
}, err => {
console.log('调起支付错误')
this.$msgbox('提示', err.data.message)
console.log(err)
})
},
/* 安卓ios支付 */
anzhuoPay (jsonStr) {

this.$msgbox('提示', '请到微信端支付')

},








wxPay (option) {
let that = this
wx.chooseWXPay({
'appId': option.appid,
'paySign': option.sign,
'timestamp': option.timestamp,
'nonceStr': option.noncestr,
'package': option.packageValue,
'signType': option.signType,
'success': function (res) {
console.log('支付成功88')
console.log(res)
if (res.errMsg === 'chooseWXPay:ok') {

that.$emit('cancelOrder')
} else if (res.errMsg === 'chooseWXPay:cancel') {
alert('支付取消')
} else if (res.errMsg === 'chooseWXPay:fail') {
alert('支付失败')
}
}
})
},

signApi () {
let urlRe = encodeURIComponent(window.location.href)
api.sign({
url: urlRe
}).then(res => {
console.log('签名res')
console.log(res)
let config = res.data
wx.config({
debug: false,
appId: config.appId,                // 必填，公众号的唯一标识
timestamp: config.timestamp,        // 必填，生成签名的时间戳
nonceStr: config.nonceStr,          // 必填，生成签名的随机串
signature: config.signature,        // 必填，签名，见附录1
jsApiList: config.jsApiList                // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
})
let options = {
title: '',
desc: '',
link: '',
imgUrl: '',
success: function () {
},
cancel: function () {
}
}
wx.ready(function (res) {
wx.onMenuShareTimeline(options)
wx.onMenuShareAppMessage(options)
})
}, err => {
console.log('签名err')
console.log(err)
})
},
isPagState (pagState, invert) {
if (pagState === 1) {
return '待付款'
} else if (pagState === 2) {
if (invert === 2) {
return '待消费'
} else {
return '待发货'
}
} else if (pagState === 3) {
return '待收货'
} else if (pagState === 5) {
return '已完成'
} else if (pagState === 6) {
return '退款中'
} else if (pagState === 7) {
return '已退款'
} else if (pagState === 8) {
return '已关闭(过期)'
} else if (pagState === 9) {
return '拼单中'
}
},
isQuantity (quantity) {
return '共' + quantity + '件商品'
},
isTotalMoney (totalMoney) {
return '实付￥' + totalMoney + '元'
},
...mapMutations({
set_vOrderGuid: 'SET_VORDERGUID'
})

},
components: {
Group,
Cell,
Panel,
HlCell,
XButton,
Loading
}
}



